* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html,
body {
  max-width: 100vw;
  overflow-x: hidden;
  font-family: var(--font-geist-sans);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* Hide scrollbar on landing page */
html:has(.scrollbar-hide),
html:has(.scrollbar-hide) body {
  -ms-overflow-style: none;
  scrollbar-width: none;
}

html:has(.scrollbar-hide)::-webkit-scrollbar,
html:has(.scrollbar-hide) body::-webkit-scrollbar {
  display: none;
}

body {
  color: hsl(var(--foreground));
  background: linear-gradient(135deg, #f0f9ff 0%, #ffffff 50%, #fef3c7 100%);
  background-attachment: fixed;
  min-height: 100vh;
}

/* Custom Scrollbar */
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

::-webkit-scrollbar-track {
  background: transparent;
}

::-webkit-scrollbar-thumb {
  background: hsl(var(--border));
  border-radius: var(--radius-sm);
}

::-webkit-scrollbar-thumb:hover {
  background: hsl(var(--muted-foreground) / 0.3);
}

/* Focus styles */
*:focus-visible {
  outline: 2px solid hsl(var(--ring));
  outline-offset: 2px;
}

/* Selection */
::selection {
  background-color: hsl(var(--primary) / 0.2);
  color: hsl(var(--primary));
}

/* Animation utilities */

/* Chat container */
.chat-container {
  display: flex;
  flex-direction: column;
  height: calc(100vh - 4rem);
}

/* Force overflow hidden on mobile for chat */
@media (max-width: 640px) {
  html, body {
    overflow-x: hidden !important;
    max-width: 100vw !important;
  }

  .chat-message-content {
    max-width: calc(100vw - 5rem) !important;
    min-width: 0 !important;
    overflow-wrap: break-word !important;
    word-break: break-word !important;
  }

  .chat-message-content > * {
    max-width: 100% !important;
    min-width: 0 !important;
    overflow: hidden !important;
  }

  [data-radix-scroll-area-viewport] {
    overflow-x: hidden !important;
    max-width: 100vw !important;
  }

  /* Ensure all flex containers in chat don't overflow */
  .chat-message-content .flex {
    max-width: 100%;
    min-width: 0;
  }

  /* Fix motion.div containers */
  .chat-message-content [style*="transform"] {
    max-width: 100% !important;
  }
}

/* Gradient text */
.gradient-text {
  background: linear-gradient(135deg, var(--color-primary-500) 0%, var(--color-primary-600) 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

/* Glass morphism */
.glass {
  background: rgba(255, 255, 255, 0.8);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
}

.glass-strong {
  background: rgba(255, 255, 255, 0.9);
  backdrop-filter: blur(30px);
  -webkit-backdrop-filter: blur(30px);
}

/* Noise texture overlay */
.noise-overlay::before {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  opacity: 0.02;
  background-image: url("data:image/svg+xml,%3Csvg viewBox='0 0 256 256' xmlns='http://www.w3.org/2000/svg'%3E%3Cfilter id='noise'%3E%3CfeTurbulence type='fractalNoise' baseFrequency='0.65' numOctaves='3' stitchTiles='stitch'/%3E%3C/filter%3E%3Crect width='100%25' height='100%25' filter='url(%23noise)'/%3E%3C/svg%3E");
  z-index: 9999;
}

/* Animated plane background */
.plane-bg {
  position: fixed;
  top: 20%;
  left: -100px;
  opacity: 0.03;
  z-index: 0;
  animation: plane-fly 30s linear infinite;
}

@keyframes plane-fly {
  0% {
    transform: translateX(-100px) translateY(0);
  }
  50% {
    transform: translateX(50vw) translateY(-30px);
  }
  100% {
    transform: translateX(calc(100vw + 100px)) translateY(0);
  }
}

/* Floating clouds */
.cloud {
  position: fixed;
  opacity: 0.03;
  z-index: 0;
}

.cloud-1 {
  top: 10%;
  animation: float-1 20s ease-in-out infinite;
}

.cloud-2 {
  top: 40%;
  animation: float-2 25s ease-in-out infinite;
}

.cloud-3 {
  top: 70%;
  animation: float-3 30s ease-in-out infinite;
}

@keyframes float-1 {
  0%, 100% { transform: translateX(-10%) translateY(0); }
  50% { transform: translateX(110%) translateY(-20px); }
}

@keyframes float-2 {
  0%, 100% { transform: translateX(110%) translateY(0); }
  50% { transform: translateX(-10%) translateY(10px); }
}

@keyframes float-3 {
  0%, 100% { transform: translateX(-5%) translateY(0); }
  50% { transform: translateX(105%) translateY(-15px); }
}

/* Typing indicator animation */
@keyframes typing-bounce {
  0%, 60%, 100% {
    transform: translateY(0);
  }
  30% {
    transform: translateY(-4px);
  }
}

.typing-dot {
  animation: typing-bounce 1.4s infinite ease-in-out;
}

.typing-dot:nth-child(1) {
  animation-delay: 0s;
}

.typing-dot:nth-child(2) {
  animation-delay: 0.2s;
}

.typing-dot:nth-child(3) {
  animation-delay: 0.4s;
}

/* Confidence bar animation */
@keyframes confidence-fill {
  from {
    width: 0%;
  }
}

.confidence-bar {
  animation: confidence-fill 0.5s ease-out forwards;
}

/* Pulse animation for pending actions */
@keyframes pulse-soft {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.7;
  }
}

.pulse-soft {
  animation: pulse-soft 2s ease-in-out infinite;
}

/* Bottom sheet backdrop */
.bottom-sheet-backdrop {
  backdrop-filter: blur(4px);
  -webkit-backdrop-filter: blur(4px);
}

/* Undo toast progress bar */
@keyframes undo-progress {
  from {
    width: 100%;
  }
  to {
    width: 0%;
  }
}

.undo-progress-bar {
  animation: undo-progress var(--undo-duration, 15s) linear forwards;
}

.undo-progress-bar.paused {
  animation-play-state: paused;
}

